/* Center Rounded Video CTA Component */

.center-rounded-video-cta__button-wrapper {
	display: flex;
	justify-content: center;
	margin-top: 1rem;
}

.center-rounded-video-cta__inner {
	background: #f7f0ec;
	border-radius: 20px;
	margin: 0 auto;
	position: relative;
	text-align: center;
	z-index: 1;
}

.container.center-rounded-video-cta__inner {
	padding: 4rem 1rem;
}

.center-rounded-video-cta__title {
	color: var(--wp--preset--color--primary);
	font-size: var(--wp--preset--font-size--xx-large);
	font-weight: 600;
	letter-spacing: -0.02em;
	line-height: 1.2;
	margin: 0 0 1rem 0;
	text-transform: uppercase;
}

.center-rounded-video-cta__video-wrapper {
	aspect-ratio: 16 / 9;
	border-radius: 24px;
	margin: 0 0 1rem 0;
	max-width: 100%;
	overflow: hidden;
	position: relative;
	transition:
		transform 0.3s ease,
		box-shadow 0.3s ease;
}

/* Video embed styling */
.center-rounded-video-cta__video-wrapper iframe,
.center-rounded-video-cta__video-wrapper video,
.center-rounded-video-cta__video-wrapper embed,
.center-rounded-video-cta__video-wrapper object {
	border: none;
	border-radius: 24px;
	height: 100%;
	left: 0;
	position: absolute;
	top: 0;
	width: 100%;
}

.center-rounded-video-cta__video-wrapper.loading {
	animation: pulse 2s infinite;
}

/* Animation for loading state */
@keyframes pulse {
	0%,
	100% {
		opacity: 1;
	}
	50% {
		opacity: 0.7;
	}
}

/* Responsive design */
@media (max-width: 768px) {
	.center-rounded-video-cta {
		padding: 3rem 0;
	}

	.center-rounded-video-cta__inner.container {
		padding: 4rem 1.5rem;
	}

	.center-rounded-video-cta__title {
		margin-bottom: 2rem;
	}

	.center-rounded-video-cta__video-wrapper {
		border-radius: 16px;
		margin-bottom: 2rem;
	}

	.center-rounded-video-cta__video-wrapper iframe,
	.center-rounded-video-cta__video-wrapper video,
	.center-rounded-video-cta__video-wrapper embed,
	.center-rounded-video-cta__video-wrapper object {
		border-radius: 16px;
	}
}

@media (max-width: 480px) {
	.center-rounded-video-cta {
		padding: 2rem 0;
	}

	.center-rounded-video-cta__video-wrapper {
		border-radius: 12px;
	}

	.center-rounded-video-cta__video-wrapper iframe,
	.center-rounded-video-cta__video-wrapper video,
	.center-rounded-video-cta__video-wrapper embed,
	.center-rounded-video-cta__video-wrapper object {
		border-radius: 12px;
	}
}
